html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Prompt", sans-serif;
  font-weight: 300;
}

img {
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

[class$=__container] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

[class$=__section] {
  padding: 5rem 0;
}

h1 {
  margin: 0;
  line-height: 1.3;
  font-weight: 700;
}

h2 {
  margin: 0;
  line-height: 1.3;
  font-weight: 700;
}

h3 {
  margin: 0;
  line-height: 1.3;
  font-weight: 700;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=__heading] {
  margin: 2rem 0;
}

a {
  font-size: 2rem;
  text-decoration: none;
  color: #111;
}

p {
  font-size: 2rem;
  line-height: 1.5;
}

.header {
  display: block;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  transition: background-color 0.2s ease-in-out;
  z-index: 2;
}
.header--bg {
  background-color: #381990;
}
.header__top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .header__top-bar {
    padding: 2rem 3.5rem;
  }
}
.header__logo-title {
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}
.header__logo-title span {
  display: block;
  color: #cdff6b;
}
.header__logo-title span {
  display: inline;
}

.hero {
  min-height: 50rem;
  max-height: 70rem;
  position: relative;
  overflow: hidden;
}
.hero__overlay {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: linear-gradient(270deg, rgba(56, 25, 144, 0.6), rgba(39, 17, 101, 0.9));
}
.hero__video {
  width: auto;
  display: block;
}
@media (min-width: 1024px) {
  .hero__video {
    width: 100%;
  }
}
.hero__content {
  text-align: center;
  color: #ffffff;
  z-index: 1;
}
.hero__heading {
  font-size: 5rem;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}
.hero__heading span {
  display: block;
  color: #cdff6b;
}
@media (min-width: 768px) {
  .hero__heading {
    font-size: 10rem;
  }
}
.hero__text {
  max-width: 50rem;
  margin: 0 auto;
}
.hero__btn {
  margin-top: 2rem;
  display: inline-block;
  padding: 1rem 2rem;
  background-color: #cdff6b;
  font-size: 2rem;
  font-weight: 700;
  color: #111;
  text-align: center;
  text-decoration: none;
}
@media (min-width: 768px) {
  .hero__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .hero__btn {
    transition: background-color 0.3s ease-in-out;
  }
  .hero__btn:hover {
    background-color: #bcff38;
    cursor: pointer;
  }
}

.section-date {
  background-color: #cdff6b;
}
.section-date__content {
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.section-date__datetime {
  font-size: 5rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .section-date__datetime {
    font-size: 7rem;
  }
}
.section-date__location {
  margin: 0.5rem;
}
@media (min-width: 768px) {
  .section-date__location {
    font-size: 3rem;
  }
}

.about-event {
  background-color: #381990;
}
@media (min-width: 768px) {
  .about-event__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.about-event__picture {
  display: flex;
}
.about-event__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #ffffff;
}
.about-event__date {
  font-size: 2rem;
  color: #cdff6b;
  font-weight: 500;
}

.artists {
  border-top: 1rem solid #cdff6b;
  background-color: #381990;
}
.artists__heading {
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}
.artists__heading span {
  display: block;
  color: #cdff6b;
}
.artists__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .artists__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.tickets {
  background-color: #cdff6b;
}
.tickets__heading {
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}
.tickets__heading span {
  display: block;
  color: #cdff6b;
}
.tickets__heading span {
  color: #111;
}
.tickets__grid {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .tickets__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.gallery {
  background-color: #381990;
}
.gallery__heading {
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}
.gallery__heading span {
  display: block;
  color: #cdff6b;
}
.gallery__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .gallery__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery__img {
  height: 100%;
  border: 0.3rem solid #cdff6b;
}

.footer {
  padding: 2rem;
  background-color: #cdff6b;
}
.footer__text {
  margin: 0;
  text-align: center;
}

.hamburguer-btn {
  display: block;
  border: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .hamburguer-btn {
    display: none;
  }
}
.hamburguer-btn__icon {
  color: #ffffff;
  width: 2.5rem;
}

.nav {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0;
  top: 99%;
  background-color: #381990;
  clip-path: circle(0% at 100% 0);
  transition: 0.4s all ease-in-out;
}
@media (min-width: 768px) {
  .nav {
    width: auto;
    flex-direction: row;
    gap: 2rem;
    position: static;
    clip-path: none;
    background-color: transparent;
  }
}
.nav--active {
  clip-path: circle(141.2% at 100% 0%);
}
.nav__link {
  padding: 1rem;
  border-bottom: 0.3rem solid transparent;
  width: 100%;
  font-weight: 500;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .nav__link {
    width: auto;
    padding: 0.3rem;
  }
}
@media (hover: hover) {
  .nav__link {
    transition: all 0.3s ease-in-out;
  }
  .nav__link:hover {
    border-color: #cdff6b;
    color: #cdff6b;
  }
}

.card-artists {
  background-color: #cdff6b;
}
.card-artists__day {
  margin: 0;
  padding: 2rem;
  font-size: 3rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
.card-artists__artist {
  padding: 2rem;
  font-weight: 700;
}
.card-artists__artist span {
  font-weight: 400;
}
.card-artists__artist:nth-child(odd) {
  background-color: #381990;
  color: #ffffff;
}

.card-ticket {
  border: 0.1rem solid #381990;
  padding: 2rem 0;
  background-color: #cdff6b;
  overflow: hidden;
  text-align: center;
}
@media (hover: hover) {
  .card-ticket {
    transition: transform 0.3s ease-in-out;
  }
  .card-ticket:hover {
    transform: scale(1.2);
  }
}
.card-ticket__name {
  padding-bottom: 2rem;
  color: #111;
  font-weight: 400;
  text-transform: uppercase;
}
.card-ticket__price {
  margin: 0;
  padding: 2rem 0;
  background-color: #381990;
  color: #cdff6b;
  font-weight: 700;
  font-size: 2.5rem;
}
.card-ticket__content {
  border-bottom: 0.1rem solid #381990;
  min-height: 18rem;
  padding: 2rem;
  font-size: 2rem;
  text-align: center;
}
.card-ticket__btn {
  margin-top: 2rem;
  display: inline-block;
  padding: 1rem 2rem;
  background-color: #381990;
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
}
@media (min-width: 768px) {
  .card-ticket__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .card-ticket__btn {
    transition: background-color 0.3s ease-in-out;
  }
  .card-ticket__btn:hover {
    background-color: #271165;
    cursor: pointer;
  }
}